import React, { useEffect, useState } from "react";
import FoodList from "./food-list";
import FoodInput from "./food-input";
import UserInput from "./user-input";
import TotalCalories from "./total-calories";

function Main() {
  const [list, setList] = useState([]);
  const [show, setShow] = useState(false);
  const [nameInput, setNameInput] = useState("");

  const [guide, setGuide] = useState(
    "Digite o nome do alimento e a quantidade a ser consumida"
  );
  const [foodph, setFoodph] = useState("Alimento");
  const [btnph, setBtnph] = useState("Continuar");
  const [inputguide, setInputguide] = useState(
    "Agora digite os valores referentes  tabela nutricional"
  );
  /* Quantidades da tabela nutricional */
  const [amount, setAmount] = useState("Quantidade");
  const [carbamount, setCarbamount] = useState("Carboidratos");
  const [protamount, setProtamount] = useState("Protenas");
  const [fatamount, setFatamount] = useState("Gorduras");
  const [addbtn, setAddbtn] = useState("Adicionar");

  function languageChange() {
    guide == "   "
      ? setGuide("Digite o nome do alimento e a quantidade a ser consumida")
      : setGuide("   ");

    foodph == "" ? setFoodph("Alimento") : setFoodph("");
    btnph == "" ? setBtnph("Continuar") : setBtnph("");
    inputguide == "    "
      ? setInputguide("Agora digite os valores referentes  tabela nutricional")
      : setInputguide("    ");

    carbamount == ""
      ? setCarbamount("Carboidratos")
      : setCarbamount("");

    protamount == ""
      ? setProtamount("Protenas")
      : setProtamount("");

    fatamount == "" ? setFatamount("Gorduras") : setFatamount("");
    amount == "" ? setAmount("Quantidade") : setAmount("");
    addbtn == "" ? setAddbtn("Continuar") : setAddbtn("");
  }

  const storage = "savedlist";

  function deleteItem(obj) {
    let newList = list.filter((it) => it.id != obj.id);
    setList(newList);
  }

  useEffect(() => {
    let savedlist = JSON.parse(localStorage.getItem(storage));
    if (savedlist) {
      setList(savedlist);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem(storage, JSON.stringify(list));
  }, [list]);

  return (
    <div>
      <UserInput
        setShow={setShow}
        name={nameInput}
        setNameInput={setNameInput}
        languageChange={languageChange}
        foodph={foodph}
        btnph={btnph}
      ></UserInput>
      <FoodInput
        list={list}
        setList={setList}
        show={show}
        setShow={setShow}
        setNameInput={setNameInput}
        inputguide={inputguide}
        amount={amount}
        carbamount={carbamount}
        protamount={protamount}
        fatamount={fatamount}
        addbtn={addbtn}
      />
      <main>
        {!list[0] ? (
          <div>
            <h1 className="guide">{guide}</h1>
          </div>
        ) : (
          <div className="meal-container">
            <div className="meals">
              <FoodList list={list} deleteItem={deleteItem} amount={amount} />
            </div>
            <TotalCalories
              list={list}
              carbamount={carbamount}
              protamount={protamount}
              fatamount={fatamount}
            />
          </div>
        )}
      </main>
    </div>
  );
}

export default Main;
